
body {
  background: #E7F6FF;
  height: 100%;
}

* {
  margin: 0;
  font-family: 'Inter', sans-serif;

}

.App {

  background: #FFFFFF;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
  max-width: 1080px;
  margin: 50px auto;
}

header {
  border-bottom: 1px solid #EAEAEA;

  img {
    margin-right: 15px;
  }

  h3, p {
    margin: 0;
  }
}

.content {
  h1 {
    margin: 0;
  }
}

.greenButtonGoBack {
  color: #FFFFFF;
  font-size: 16px;
  font-weight: 500;
  padding: 18px;
  background: #9DD558;
  border-radius: 18px;
  border: none;
  cursor: pointer;
  width: 245px;
  margin-top: 20px;


  &:hover {
    img {
      transform: rotate(180deg) translateX(3px);
    }

    background: lighten(#9DD558, 5%);
  }

  &:active {
    background: darken(#9DD558, 5%);

  }

  img {
    position: relative;
    top: 1px;
    transform: rotate(180deg);
    margin-right: 15px;
    transition: transform 0.1s ease-in-out;
  }
}

.button {
  width: 32px;
  height: 32px;
  background: #FFFFFF;
  border: 1px solid #F2F2F2;
  border-radius: 8px;
  cursor: pointer;
}

.search-block {
  border: 1px solid #F3F3F3;
  border-radius: 10px;
  padding: 0 15px;
}

input {
  border: none;
  padding: 13px;
  font-size: 16px;
  width: 200px;
}


.overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;

  .drawer {
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 420px;
    height: 100%;
    right: 0;
    background: #FFFFFF;
    box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);
    padding: 30px;
    overflow: auto;
    transition: 5s ease-in-out;

    .items {
      flex: 1;
      overflow: auto;
      margin-bottom: 40px;
    }

    h2 {
      margin: 0;

      img {
        cursor: pointer;
        opacity: 0.5;
      }

      img:hover {
        opacity: 1;
      }

    }

    .cartItem {
      border: 1px solid #F3F3F3;
      border-radius: 20px;
      overflow: hidden;
      padding: 20px;



      p {
        font-size: 16px;
        margin: 0;
      }

      b {
        font-size: 14px;
        margin: 0;
      }

      .removeBtn {
        opacity: 0.5;
        cursor: pointer;
        transition: box-shadow 0.2s ease-in-out;
      }

      .removeBtn:hover {
        opacity: 1;
      }

    }

    .cartTotalBlock {

      ul {
        display: block;
        margin-bottom: 40px !important;

        li {
          display: flex;
          align-items: flex-end;
          margin-bottom: 20px;
        }

        div {

          flex: 1;
          height: 1px;
          border-bottom: 1px dashed #DFDFDF;
          position: relative;
          top: -4px;
          margin: 0 7px;
        }
      }

      .greenButton {
        position: relative;

        &:hover {
          img {
            transform: translateX(5px);
          }
        }

        &:active {
          img {
            transform: translateX(-5px);
          }
        }

        img {
          position: absolute;
          right: 50px;
          top: 20px;
          transition: transform 0.1s ease-in-out;
        }
      }

      .greenButton {
        color: #FFFFFF;
        font-size: 16px;
        font-weight: 500;
        width: 100%;
        padding: 18px;
        background: #9DD558;
        border-radius: 18px;
        border: none;
        cursor: pointer;
        transition: background 0.2s ease-in-out;

        &:hover {
          background: lighten(#9DD558, 5%);
        }

        &:active {
          background: darken(#9DD558, 5%);

        }
      }

    }

    .cartEmpty {
      text-align: center;
      p {
        width: 280px;
        line-height: 24px;
      }
    }
  }
}



.EmptyPage {
  margin: 5% auto;

}


























